import React, { useState } from 'react';
import moment from 'moment';

export const MonthView = ({ today }) => {
  const [currentMonth, setCurrentMonth] = useState(today.getMonth());
  const [currentYear, setCurrentYear] = useState(today.getFullYear());
  const [allMonths, setAllMonths] = useState(moment.monthsShort());

  return (
    <div className="">
      <div className="big-year">{currentYear}</div>
      <div className="months">
        {allMonths.map((month, index) => 
          <span key={month} id={index + 1} className="each-month">
            {month}
          </span>
        )}
      </div>
      <hr className="month-line"></hr>
      <table className="">
        <thead>
          <tr>
            <th class="days-of-week">Sun</th>
            <th class="days-of-week">Mon</th>
            <th class="days-of-week">Tue</th>
            <th class="days-of-week">Wed</th>
            <th class="days-of-week">Thu</th>
            <th class="days-of-week">Fri</th>
            <th class="days-of-week">Sat</th>
          </tr>
        </thead>
        <tbody id="calendar-body"></tbody>
      </table>
    </div>
  );
};
